<template>
  <div class="barbershops">
    <app-table
      title="Barbearias"
      :currentPage="currentPage"
      :headers="headers"
      :items="itemsModel"
      :loading="loading"
      :qtdPages="qtdPages"
      :statusItems="statusItems"
      @onActionClicked="callAction"
      @onChangeOrder="changeOrder"
      @onChangeStatus="changeStatus"
      @onPageChanged="changePage"
      @onSearch="searchValue"
    />
  </div>
</template>

<script>
import Axios from '@/plugins/axios'
import AppTable from '@/components/moderators/Table';
import DateMixin from '@/mixins/DateMixin';
import SnackbarMixin from '@/mixins/SnackbarMixin';
import TextMixin from '@/mixins/TextMixin';

export default {
  name: 'Barbershops',
  components: { AppTable },
  mixins: [ DateMixin, SnackbarMixin, TextMixin ],
  data: () => ({
    currentPage: null,
    headers: [
      { text: '#', value: 'id', align: 'center', width: '60' },
      { text: 'Imagem', value: 'barberImage', align: 'center', width: '80' },
      { text: 'Nome', value: 'name', align: 'start' },
      { text: 'Telefone', value: 'contact', align: 'start' },
      { text: 'Cadastro', value: 'created_at', align: 'start' },
      { text: 'Status', value: 'barbershopStatus', align: 'start' },
      { text: 'Açoes', value: 'actions', align: 'center', width: '60' },
    ],
    items: [],
    loading: true,
    order: null,
    qtdPages: 1,
    search: null,
    status: null,
    statusItems: [
      { text: 'Todas', value: null },
      { text: 'Aguardando aprovação', value: 1 },
      { text: 'Ativas', value: 2 },
      { text: 'Bloqueadas', value: 3 }
    ]
  }),
  computed: {
    itemsModel () {
      return this.items.map((barbershop) => {
        const action = [
          {
            icon: barbershop.status.id === 2 ? 'lock_open' : 'lock',
            class:  barbershop.status.id === 2 ? 'blue' : 'gray3',
            action:  barbershop.status.id === 2 ? 'block' : 'unblock',
            text: barbershop.status.id === 2 ? 'Bloquear' : 'Desbloquear'
          }
        ];
        let address = ''
        if (barbershop.address) {
          address = `${barbershop.address.neighborhood} - ${barbershop.address.city}`
        }
        const phone = this.setPhoneMask(barbershop.phone_number) || 'sem telefone' 
        return {
          id: barbershop.id,
          name: barbershop.name,
          contact: phone,
          barberImage: barbershop.image_url,
          created_at: this.formatDateTimeToStringPtBr(new Date(barbershop.created_at)),
          address: address,
          barbershopStatus: barbershop.status,
          actions: action,
        };
      });
    }
  },
  mounted () {
    this.getBarbershops();
  },
  methods: {
    blockBarbershop(barbershop) {
      const errorMessage = 'Não foi possível bloquear a barbearia!'
      Axios.put(`${this.$url_api}/barbershop/${barbershop.id}/block`)
      .then(response => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            const index = this.items.findIndex(b => Number(barbershop.id) === Number(b.id))
            if (index > -1) {
              let item = this.items[index]
              item.status = {
                id: this.$BARBER_STATUS.BLOQUEADO,
                name: 'Bloqueada'
              }
              this.items.splice(index, 1, item)
            }
            this.showSnackbar(response.data.corpo)
          } else this.setErro(errorMessage)
        } else this.setErro(errorMessage)
      }).catch(() => this.setErro(errorMessage))
    },
    callAction(action, item) {
      switch (action) {
        case 'block':
          this.blockBarbershop(item);
          break;
        case 'unblock':
          this.unblockBarbershop(item);
          break;
      }
    },
    changePage(page) {
      this.currentPage = page;
      this.getBarbershops();
    },
    changeOrder (order) {
      this.order = order
      this.getBarbershops()
    },
    changeStatus (status) {
      this.status = status
      this.getBarbershops()
    },
    getBarbershops () {
      const message = 'Não foi possível buscar as barbearias!'
      this.loading = true;
      let url = `${this.$url_api}/moderators/barbershop`;
      let operator = '?';
      if (this.search) {
        url += `${operator}search=${this.search}`;
        operator = '&';
      }
      if (this.order) {
        url += `${operator}order=${this.order}`;
        operator = '&';
      }
      if (this.status) url += `${operator}status=${this.status}`;
      
      if (this.currentPage) url += `${operator}page=${this.currentPage}`;
      Axios(url)
        .then((response) => {
          this.loading = false;
          if (response.status === 200) {
            if (response.data.codigo === 200) {
              this.currentPage = response.data.corpo.current_page;
              this.qtdPages = response.data.corpo.last_page;
              this.items = response.data.corpo.data
            } else this.setError(response.data.corpo);
          } else this.setError(message);
        })
        .catch(() =>this.setError(message));
    },
    searchValue(val) {
      this.currentPage = null;
      this.search = val;
      this.getBarbershops();
    },
    setError(message) {
      this.loading = false;
      this.showSnackbarErro(message);
    },
    unblockBarbershop(barbershop) {
      const errorMessage = 'Não foi possível desbloquear a barbearia!'
      this.$axios.put(`${this.$url_api}/barbershop/${barbershop.id}/unblock`)
      .then(response => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            const index = this.items.findIndex(b => barbershop.id === b.id)
            if (index > -1) {
              let item = this.items[index]
              item.status = {
                id: this.$BARBER_STATUS.ATIVO,
                name: 'Ativada'
              }
              this.items.splice(index, 1, item)
            }
            this.showSnackbar(response.data.corpo)
          } else this.setErro(errorMessage)
        } else this.setErro(errorMessage)
      }).catch(() => this.setErro(errorMessage))
    }
  }
}
</script>